import React from "react";
import SkillsCard from "./skillsCard";

const Skill = () => {
  return (
    <section className='py-16 bg-slate-100' id="skill">
        <div className="container">
            <div className="w-full text-center">
                <h1 className="font-gothamBlack text-primary text-4xl">Skills</h1>
                <p className='font-semibold text-lg mt-3 leading-none'>Beberapa Skill atau Kemampuan yang Saya Kuasai</p>
            </div>
            <div className="flex flex-wrap mt-10 px-4">
                <SkillsCard />
            </div>
        </div>
    </section>
  );
};

export default Skill;
