import { useState, useEffect } from "react";
import React from "react";
import {
  Navbar,
  MobileNav,
  Typography,
  // Button,
  IconButton,
} from "@material-tailwind/react";

const Navigation = () => {
  const [openNav, setOpenNav] = useState(false);
 
  useEffect(() => {
    window.addEventListener("resize", () => window.innerWidth >= 960 && setOpenNav(false));
  }, []);
 
  const navList = (
    <ul className="mb-4 mt-2 flex flex-col gap-2 lg:mb-0 lg:mt-0 lg:flex-row lg:items-center lg:gap-6">
      <Typography
        as="li"
        variant="small"
        className="p-1 font-normal text-primary"
      >
        <a href="#tentang" className="flex items-center hover:text-black">
          About
        </a>
      </Typography>
      <Typography
        as="li"
        variant="small"
        className="p-1 font-normal text-primary"
      >
        <a href="#pendidikan" className="flex items-center hover:text-black">
          Pendidikan
        </a>
      </Typography>
      <Typography
        as="li"
        variant="small"
        className="p-1 font-normal text-primary"
      >
        <a href="#skill" className="flex items-center hover:text-black">
          Skill
        </a>
      </Typography>
      <Typography
        as="li"
        variant="small"
        className="p-1 font-normal text-primary"
      >
        <a href="#portfolio" className="flex items-center hover:text-black">
          Portfolio
        </a>
      </Typography>
      <Typography
        as="li"
        variant="small"
        className="p-1 font-normal text-primary"
      >
        <a href="#unduhcv" className="flex items-center hover:text-black">
          Unduh CV
        </a>
      </Typography>
    </ul>
  );
  return (
    <Navbar className="mx-auto w-full py-2 px-4 lg:px-8 lg:py-4">
      <div className="container mx-auto flex items-center justify-between text-primary">
        <Typography
          as="a"
          href="#home"
          className="mr-4 cursor-pointer py-1.5 font-semibold"
        >
          aditbagja
        </Typography>
        <div className="hidden lg:block">{navList}</div>
        {/* <Button variant="gradient" size="sm" className="hidden lg:inline-block bg-primary hover:opacity-80">
          <span>Toggle Light</span>
        </Button> */}
        <IconButton
          variant="text"
          className="ml-auto h-6 w-6 text-inherit hover:bg-transparent focus:bg-transparent active:bg-transparent lg:hidden"
          ripple={false}
          onClick={() => setOpenNav(!openNav)}
        >
          {openNav ? (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              className="h-6 w-6"
              viewBox="0 0 24 24"
              stroke="currentColor"
              strokeWidth={2}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              stroke="currentColor"
              strokeWidth={2}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
          )}
        </IconButton>
      </div>
      <MobileNav open={openNav}>
        <div className="container mx-auto">
          {navList}
          {/* <Button variant="gradient" size="sm" fullWidth className="mb-2 bg-primary">
            <span>Toggle Light</span>
          </Button> */}
        </div>
      </MobileNav>
    </Navbar>
  );
};

export default Navigation;

